<template>
	<div class="auto-repay-success">
		<tip-page :tipTitle="tipTitle" :tipMsg="tipMsg" tipButton="" @submit="back">
		</tip-page>
	</div>
</template>

<script>
	
export default {
	data() {
		return {
			tipTitle: '',
			tipMsg: ''
		};
	},
	created() {
		let type = this.$route.params.type;
		this.tipTitle = `${type}`;
		this.tipMsg = `${type}`;
	},
	methods: {
		back() {
			this.$router.push({name: 'CreditManage'});
		}
	}
};

</script>


