<template>
	<div class="pwd-manage-serach-apply-set">
        <div class="field-part">
            <mt-cell title="" :value="card"></mt-cell>  
        </div>
        <div class="field-part">
            <div class="head-hint"></div>
            <mt-field v-model="card1" label="CVN2" placeholder="3"></mt-field>
        </div>
        <div class="tip-hint">
            
        </div>
        <div class="field-part">
    		<div class="head-hint"></div>
    		<cell-password :options="cellPassword1"></cell-password>
    		<cell-password :options="cellPassword2"></cell-password>
        </div>
        <div class="tip-hint">
            
        </div>
		<div class="button-bottom">
			<mt-button @click.native="submit" type="primary" size="large"></mt-button>
			<dialog-sms-verify ref="dialogSmsVerify" :options=" dialogSmsVerify "></dialog-sms-verify>
		</div>
	</div>
</template>

<script>

import service from '@/services/credit/manage';

export default {
	data() {
		return {
			card: '',
			card1: '',
			cellPassword1: {
                title: '',
                rule: 'number' // number or mix
            },
            cellPassword2: {
                title: '',
                rule: 'number' // number or mix
            },
            dialogSmsVerify: {
                title: '',
                btnConfirmText: '',
                btnGetText: ''
            }
		};
	},
	methods: {
		submit() {
            this.$refs.dialogSmsVerify.show().then((data) => {
                this.applyTransactionPwd();
            }, (data) => {
            });
        },
        applyTransactionPwd() {
        	service.applyTransactionPwd()
        	.then((data) => {
        		this.$router.push({name: 'CreditManagePwdTransactionSuccess', params: {type: 'apply', fromAdd: this.$route.params.fromAdd}});
        	}, (err) => {
        		console.log(err);
        	});
        }
	},
	created() {
		this.card = this.$route.params.card;
	}
};

</script>


