<template>
	<div class="more-service-apply-process">
		<mt-cell title=""></mt-cell>
		<mt-field v-model="idCard" label="" type="text" placeholder=""></mt-field>
		<div class="field-action">
            <mt-button @click.native="showDialogSmsVerify()" type="primary"></mt-button>
            <dialog-sms-verify ref="dialogSmsVerify" :options=" dialogSmsVerify "></dialog-sms-verify>
		</div>
	</div>
</template>

<script>

import service from '@/services/credit/more-service';

export default{
	data() {
		return {
			idCard: '',
            dialogSmsVerify: {
                title: '',
                btnConfirmText: '',
                btnGetText: ''
            }
		};
	},
	methods: {
        back() {
        	history.back();
        },
        processSearch() {
            service.processSearch()
            .then((data) => {
                this.$router.push({name: 'ApplyProcessDetail', params: {detail: data.data.data}});
            }, (err) => {
                console.log(err);
            });
        },
        showDialogSmsVerify() {
            if(this.$validate.isEmpty(this.idCard)){
                this.$msg('');
                return;
            }
            this.$refs.dialogSmsVerify.show().then((data) => {
                this.processSearch();
            }, (data) => {
            });
        }
	},
	created() {
	}
};

</script>

<style lang="scss">
.more-service-apply-process{
}
</style>


