<template>
	<div class="pwd-manage-serach-success">
		<tip-page :tipTitle="msg" tipButton="" @submit="back">
		</tip-page>
	</div>
</template>

<script>
	
export default {
	data() {
		return {};
	},
	computed: {
		msg: function(){
			let type = this.$route.params.type;
			if(type === 'apply'){
				return '';
			}else if(type === 'update'){
				return '';
			}else{
				return '';
			}
		}
	},
	methods: {
		back() {
			this.$router.push({name: 'CreditManage'});
		}
	}
};

</script>

<style lang="scss">
.pwd-manage-serach-success{
}
</style>


