import ajax from '@/services/ajax';
import api from '@/services/api';

export default {

    /*  */
    autoRepaySearch: data => {
        return ajax.postAfterLoginQuery('015028', data, api.autoRepaySearch);
    },
    /*  */
    setAutoRepay: data => {
    	return ajax.postAfterLoginUpdate('015028', data, api.setAutoRepay);
    },
    /*  */
    getSetServiceDetail: data => {
    	return ajax.postAfterLoginQuery('015024', data, api.getSetServiceDetail);
    },
    /*  */
    updateSetService: data => {
    	return ajax.postAfterLoginUpdate('015024', data, api.updateSetService);
    },
    /*  */
    applySearchPwd: data => {
    	return	ajax.postAfterLoginUpdate('', data, api.applySearchPwd);
    },
    /*  */
    updateSearchPwd: data => {
    	return ajax.postAfterLoginUpdate('', data, api.updateSearchPwd);
    },
    /*  */
    resetSearchPwd: data => {
    	return ajax.postAfterLoginUpdate('', data, api.resetSearchPwd);
    },
    /*  */
    applyTransactionPwd: data => {
    	return	ajax.postAfterLoginUpdate('', data, api.applyTransactionPwd);
    },
    /*  */
    updateTransactionPwd: data => {
    	return ajax.postAfterLoginUpdate('', data, api.updateTransactionPwd);
    },
    /*  */
    resetTransactionhPwd: data => {
    	return ajax.postAfterLoginUpdate('', data, api.resetTransactionhPwd);
    },
    /*  */
    setCreditAlias: data => {
    	return ajax.postAfterLoginUpdate('027001', data, api.setCreditAlias);
    },
    /*  */
    setCreditCost: data => {
    	return ajax.postAfterLoginUpdate('', data, api.setCreditCost);
    },
    /*  */
    deleteCredit: data => {
    	return ajax.postAfterLoginUpdate('', data, api.deleteCredit);
    }

};



