import ajax from '@/services/ajax';
import api from '@/services/api';

export default {
    /*  */
    // 
    processSearch: data => {
        return ajax.postBeforeLoginQuery('', data, api.processSearch);
    },
    /*  */
    // 
    checkCredit: data => {
        return ajax.postBeforeLoginQuery('', data, api.checkCredit);
    },
    // CVN2
    checkCreditMessage: data => {
        return ajax.postBeforeLoginQuery('015030', data, api.checkCreditMessage);
    },
    // 
    // checkIsApplySearchPwd: data => {
    //     return ajax.postBeforeLoginQuery('', data, api.checkIsApplySearchPwd);
    // },
    // 
    activeCredit: data => {
    	return ajax.postBeforeLoginQuery('', data, api.activeCredit);
    },
    /*  */
    // 
    submitQuickLoss: data => {
        return ajax.postAfterLoginUpdate('015032', data, api.submitQuickLoss);
    },
    /*  */
    // 
    submitPatchCard: data => {
        return ajax.postAfterLoginUpdate('015031', data, api.submitPatchCard);
    }
};



