<template>
    <div class="consume">
        <index ref="child" ls='credit-consume'>
            <template slot="info">
                <p class="info">500()</p>
            </template>

            <div class="field-action" slot="btn">
                <mt-button type="primary" size="large" @click="doApply"></mt-button>
            </div>

            <template slot="process">
                <p class="title">
                    
                </p>
                <p>
                    >>>()>>>
                </p>
            </template>
            
            <template slot="warn">
                <p class="title">
                    
                </p>
                <p>
                    1369121824
                </p>
                <p>
                    2
                </p>
            </template>
        </index>
    </div>
</template>

<script>
    import Index from '../public';
    export default {
        name: 'consume',
        components: {
            Index
        },
        methods: {
            doApply() {
                this.$router.push({ name: 'CreditConsumeList' });
            }
        },
        beforeRouteLeave(to, from, next) {
            if (to.meta['groupName'] === from.meta['groupName']) {
                this.$ls.setItem('credit-consume', this.$refs.child.formData);
            } else {
                this.$ls.removeItem('credit-consume');
            }
            next(true);
        }
    };

</script>


