<template>
	<div class="pwd-manage-transaction-apply">
		<div class="head-hint"></div>
		<div class="field-part">
			<mt-field v-model="card" label="" placeholder=""></mt-field>
		</div>
		<div class="tip-hint">
			
		</div>
		<div class="button-bottom">
			<mt-button @click.native="next" type="primary" size="large" :disabled="!card"></mt-button>
		</div>
	</div>
</template>

<script>
	
export default {
	data() {
		return {
			card: ''
		};
	},
	methods: {
		next() {
			this.$router.push({name: 'CreditManagePwdTransactionApplySet', params: {card: this.card}});
		}
	}
};

</script>


