import ajax from '@/services/ajax';
import api from '@/services/api';

// 
// flagType 0- 1- 2+ 3 4
export function getCommonAccountList(data) {
    return ajax.postAfterLoginQueryList('012020', data, api.getCommonAccountList);
}

// 
export function getCreditList(data) {
    return ajax.postAfterLoginQueryList('012022', data, api.getCreditList);
}

// banner
// PICTYPE 2-
export function getBannerImage(data) {
	return ajax.postBeforeLoginQueryList('011301', data, api.getBannerImage);
}

// 
export function getCommonAccountDetail(data) {
    return ajax.postAfterLoginQuery( '017403', data, api.getCommonAccountDetail);
}

// 
export function getMsgCode(data) {
    return ajax.postAfterLoginQuery( 'GETMSGCODE', data, api.getMsgCode);
}

// 
export function checkMsgCode(data) {
    return ajax.postAfterLoginQuery( 'CHECKMSGCODE', data, api.checkMsgCode);
}


